* {
  margin: 0;
  padding: 0;
}

.container{
 background: linear-gradient(to right bottom, #1fd5d5, #c89666);
 width: 100%;
 min-height: 100vh;
 display: flex;
 justify-content: center;
 align-items: center;
}

.app-wrapper{
  background-color: #0871d4;
  min-width: 450px;
  min-height: 650px;
  padding: 30px;
  box-sizing: border-box;
  border-radius: 10px;
  box-shadow: 3px 6px 40px #000;
  margin-bottom: 10px;
}

.header h1{
  color: #fff;
  text-align: center;
  margin: 30px 0;
}

.task-input{
  outline: none;
  width: 260px;
  padding: 15px;
  margin-right: 25px;
  font-size: 20px;
  color: rgb(0, 0, 0);
  background-color: rgb(255, 255, 255);
  border: 1px solid#f80505;
  border-radius: 10px;
}

.button-add{
  width: 70px;
  padding: 15px 15px;
  font-size: 20px;
  border-radius: 10px;
  border: 0;
  background-color: #67eb33;
  cursor: pointer ;
}

.list-item{
  display: flex;
  margin: 20px 0;
  border: 1px solid rgb(227, 223, 223);
  border-radius: 10px;
  padding: 10px;
  max-height: 30px;
}

.button-complete, .button-delete, .button-edit{
  border: none;
  outline: none;
  cursor:pointer;
  background-color: transparent;
  font-size: 28px;
  border-radius: 50px;
}

.button-complete{
  color: #ff6c6c;
  margin-right: 10px;
}

.button-edit{
  color: #e2d029;
  margin-right: 10px;
}

.complete{
  text-decoration-style: solid;
  text-decoration-line: line-through;
  text-decoration-color: #ae0202;
  opacity: 0.6;
}

.list{
  width: 220px;
  background-color: #0871d4;
  border: none;
  color: #fff;
  font-size: 20px;
  padding-left: 10px;
  margin-right: 15px;
}

input:focus, button:focus{
  outline: none;
}